const jobs = [{"entity": "Earthmover", "title": "Xarray Community Developer", "url": "https://jobs.gusto.com/postings/earthmover-xarray-community-developer-498dca94-335e-4d5c-a6c7-83ca19772512", "percentTime": 100, "percentOSS": 100, "deadline": "2024-09-01", "expires": "2024-09-01", "location": "Remote", "description": "Join the Earthmover team as a open source community developer and help us make Xarray the best tool for working with scientific data.\n\n**About Earthmover**:\n\n[Earthmover](https://earthmover.io/) is building products that solves the biggest storage and compute challenges of our time for users in the deep sciences - think climate, earth observations, biotech, machine learning, and AI.\n\nOur first product, [Arraylake](https://docs.earthmover.io/), is a data lake platform for multi-dimensional scientific array data. It enables users to manage and process array data in open standard cloud-native formats, with features like versioning, cataloging, collaboration and access control on top. Arraylake was built to integrate seamlessly with the open source scientific Python ecosystem, and provides an interface via the [Zarr](https://zarr.dev/) project. Through the [Zarr interface](https://zarr.readthedocs.io/en/stable/), scientific data users interact with data via libraries such as [Xarray](https://xarray.dev/), [AnnData](https://anndata.readthedocs.io/en/latest/), [Cooler](https://cooler.readthedocs.io/en/latest/), [Napari](https://napari.org/), and others.\n\nOur team has deep experience developing and maintaining open source software libraries across the scientific Python ecosystem. We\u2019re now looking to expand the team to include a focus on building and supporting Xarray *specifically for the biomedical research community (Note: t*his position will be partially funded by a grant from the Chan Chan Zuckerberg Initiative).\n\nEarthmover values a diverse workplace and **strongly** encourages women, people of color, LGBT individuals, people with disabilities, members of ethnic minorities, foreign-born residents, and veterans to apply. We want *you*.\n\n**Job Description**:\n\nThis role will focus on building connections, both technical and social, between the Xarray project and the biomedical research community. We want you to join the Earthmover team to do a mix of technical engineering work and community stewarding, serving the needs of biomedical researchers in fields like microscopy, genomics, and neurophysiology.\n\n**Responsibilities**:\n\nWe expect this role to be\n\n- 50% Targeted feature development, integration work, and maintenance work aimed at accelerating the adoption of Xarray across the biomedical community. There are a number of high-impact projects in the queue waiting for an eager developer to jump in.\n- 30% Outreach (office hours, blog posts, conferences).\n- 20% Community coordination and support (integration support, issue triage)\n\n**You\u2019re a great fit if you**\n\n- Have experience developing open source projects in the Scientific Python ecosystem like NumPy, Pandas, Xarray, Dask, and/or Zarr. Bonus points if you\u2019ve already been contributing to an open source project.\n- Have a background that intersects with one or more biomedical research areas. This could include prior research experience and/or supporting research in a data science, data engineering, and/or software engineering capacity.\n- Have a passion for seeing community software projects thrive.\n- Want to help the the biomedical research community use open software and open data to solve hard problems.\n\n**Our open source stack**:\n\n- [Xarray](https://xarray.dev/) is the focus of this hire. It provides a high-level data model and API for working with labeled N-dimensional arrays. Think of it it like Pandas for multidimensional data.\n- [Zarr](https://zarr.dev/) is a community developed data format for chunked, compressed multidimensional arrays. Zarr-Python is the interface to Zarr used by Xarray, Arraylake, and many other libraries.\n- Both Xarray and Zarr are built to integrate with the rest of the Scientific Python ecosystem. Think NumPy, SciPy, Scikit-learn, Dask, Matplotlib, etc.\n\n**Benefits**:\n\n- Company equity\n- Health insurance through UnitedHealthcare - up to 99% of premium covered for employee under basic plan\n- 20 days of PTO annually with a quarterly time off minimum\n- 10 personal days annually\n- 7 paid company holidays\n- 12 weeks of paid family leave\n- Quarterly company off-sites\n\nEarthmover is an equal-opportunity employer. Applicants will not be discriminated against because of race, color, creed, sex, sexual orientation, gender identity or expression, age, religion, national origin, citizenship status, disability, ancestry, marital status, veteran status, medical condition or any protected category prohibited by local, state or federal laws.\n\nAuthorization to work in the US is required for this position.\n\nEarthmover is currently an all-remote company; however, we are moving towards consolidating \u201cpods\u201d of team members in NYC, Denver, Seattle, and San Francisco.\n\n**Salary**: $135,000 - $150,000 per year\n", "date": "2024-06-17", "id": "2024-06-17_earthmover"}, {"entity": "Quansight LLC", "title": "Senior Open Source Infrastructure Engineer", "url": "www.quansight.com/careers", "percentTime": 100, "percentOSS": 100, "deadline": "2024-08-01", "expires": "2024-08-01", "location": "Remote", "description": "We are seeking a fully remote, Senior Open Source Infrastructure Engineer to join our team at Quansight. \nIn this role, you will support Quansights growing cloud and on-premises infrastructure and help make them more reliable, scalable, and efficient. You will work with our team to address support issues from our clients and collaborators, explore emerging technologies in the Cloud and DevOps spaces, and design and implement cloud computing systems with the rest of our infrastructure team.\n\nIn this position, you'll get to\nContribute to nebari (https://nebari.dev), an open source Data Science platform built on JupyterHub, Dask, and other tools from the PyData ecosystem. Nebari uses Terraform and Helm to deploy these tools to Kubernetes in multiple clouds and on premises.\nParticipate in upstream open source communities we rely on (such as JupyterHub, BinderHub, Dask, etc.) in partnership with the established leaders of those communities.\nDeploy and ensure the reliable operation of Quansights and clients infrastructure under the direction of senior team members.\nCollaborate with a fully distributed team - team members are expected to communicate and collaborate proactively to allocate effort and maximize the teams impact.\nPosition Requirements\nRequired:\nExperience using infrastructure-as-code tooling such as Terraform, Pulumi, Cloudformation to deploy infrastructure.\nExperience running production workloads in at least one major cloud platform (AWS, Azure, GCP)\nExperience developing tools in a general purpose programming language (eg. Python).\nExperience building Continuous Integration and Continuous Delivery (CI/CD) pipelines using tools such as GitHub actions, Gitlab CI/CD, Circle CI, etc.\nFamiliarity with modern software engineering practices  including automated tests, code review, version control, observability, etc.\nExperience containerizing and deploying applications to platforms such as Kubernetes, AWS ECS, and Google Cloud Run.\nComfortable working independently and reaching out for feedback and support as needed.\nExperience collaborating and coordinating work via online platforms, such as GitHub, GitLab, or BitBucket, and distributed revision control.\nAbility to constructively receive feedback.\n\nWhile this is a remote position, we are looking for candidates with significant time overlap with US Central and Eastern time zones due to the location of many of our infrastructure team members and collaborators\n\nBonus points (not required, but these would be a great advantage)\nExperience working on geographically distributed teams.\nExperience working with or contributing to open-source projects.\nExposure to the Python Data Science stack - Pandas, Numpy, Dask, etc.\nExperience with the Jupyter ecosystem and other tools for interactive computing.\nExperience with common data science methods, platforms, workflows, and infrastructures; with data management systems, practices, and standards; and the capacity to gain familiarity with new related topics.\nExperience with Machine Learning Operations (MLOps) platforms, workflows, and infrastructures and the capacity to gain familiarity with new related topics.\nCandidate journey\nApplication: Once you have applied, a recruiter will review your application to determine if your work experience and skills align with the necessary competencies of this position. This recruiter will be in touch to schedule some time to connect if they determine that you could be a good fit for Quansight.\nHR interview: This is an opportunity to get to know each other. We will tell you a little more about who we are, and we look forward to hearing about your past experiences and what you are looking for in your next role. We want to know why you are interested in joining us and what questions you have about our team and mission. This will be scheduled as a 30-minute Google Meet.\nInterview loop: If you and your recruiter still feel you could be well-suited for this position following your call, we will arrange our interview loop with some team members. This will allow you to learn more about the team and the organization and for us to gain a deeper understanding of your skills and experience. For this particular role, the interview loop is as follows:\nPre-Interview Assignment: Please choose one out of the three following options to complete prior to interviewing with our team.\nTake Home Assessment. We will share a github repository with you that includes instructions for an infrastructure related task. You will create a Pull Request (PR) for us to review that accomplishes the task as specified.Please only devote up to 4 hours on this assignment.\nPoint us to a substantial, public, infrastructure related Pull Request that you have authored and come prepared for a code walkthrough during your interview\nSend us another infrastructure related codebase you have written and come prepared for a code walkthrough during your interview\nTechnical interviews: we will schedule two separate interviews with Quansight team members (these will be potential teammates or cross-functional team members.)\nHiring manager interview: in this one-hour interview, you will meet the hiring manager for this position. Questions in this interview will focus on your career, how you handle certain situations and your general approach to collaboration and work. We also factor in time for you to ask questions about the role and the organization.\n\nFollowing the completion of all interviews, our hiring team will make a final decision, and the recruiter will be in touch to share the outcome of your interviews. If the team wants to move forward, the recruiter will discuss this and the details of our proposed offer with you.\n", "date": "2024-05-18", "id": "2024-05-18_quansight"}, {"entity": "Quansight LLC", "title": "Director of Professional Services - open source", "url": "www.quansight.com/careers", "percentTime": 100, "percentOSS": 100, "deadline": "2024-12-12", "expires": "2024-12-12", "location": "Remote", "description": "We are seeking an innovative Director of Professional Services - Open Source with leadership experience and an understanding of the PyData ecosystem to join our team. You will manage and grow Quansight\u2019s technical consulting business in a target domain.\n\nKey Qualifications\nDeep technical expertise in at least one area:\nNumerical Computing\nDistributed & High-Performance Computing\nInfrastructure / Cloud\nData Science / Machine Learning / AI\nMLOps\nData Engineering\nContributions to open source projects or participation in open source communities\nExperience in one or both of the following:\nExtensive track record in client management\nExperience leading technical project teams.\nWhat You will Do\n\nAs a Director of Professional Services - Open Source, your role will be a mix of project definition, solution architecture, sales engineering, technical leadership, and people management. You will be expected to interface with clients and translate their needs into technical solutions. You will typically oversee several projects with a diverse set of clients in different verticals.\n\nResponsibilities\nIdentify, scope, architect, resource, and oversee the delivery of large end-to-end projects for clients\nCollaborate with project managers and other staff members to develop timelines for solutions.\nAssess the systems architecture currently in place and work with technical staff to recommend solutions to improve it.\nAssess and evaluate risk as required.\nConsistently monitor the scope of projects.\nUnderstand how individual work fits into the broader organizational picture.\nKeep skills updated and remain proactively aware of happenings and current events in the industry.\nMentor, educate, and train colleagues.\nResolve technical problems as they arise.\nInform various stakeholders about any problems with the current technical solutions being implemented.\nProvide updates to stakeholders on product development processes, costs, and budgets\nQualifications\n5+ years of work experience in a relevant role; ideally professional services/consulting\nExperience serving as a leader for the full life-cycle of projects from requirements gathering, and development, all the way through to implementation & roll-out.\nExpertise in one or more core technology areas: Numerical Computing, Distributed & High-Performance Computing, Infrastructure/Cloud, Data Science/Machine Learning/AI, MLOps, and Data Engineering.\nExcellent interpersonal communication skills and the ability to explain complex technical topics in an easily digestible manner.\nIn-depth understanding of Python\nStrong organizational and leadership skills.\nContributions to open source projects\nPrior consulting experience or experience working in a client-facing environment - a plus\n\nPlease keep in mind that we are describing the background we imagine would best fit the role. Even if you do not meet all of the requirements and are confident that you are up for the task, we want to get to know you!\n\nCandidate Journey\nApplication: Once you have applied, a recruiter will review your application to determine if your work experience and skills align with the necessary competencies of this position. This recruiter will be in touch to schedule some time to connect if they determine that you could be a good fit for Quansight.\nHR Interview: This is an opportunity to get to know each other. We will tell you a little more about who we are, and we look forward to hearing about your past experiences and what you are looking for in your next role. We want to know why you are interested in joining us and what questions you have about our team and mission. This will be scheduled as a 30-minute Google Meet.\u00a0\nInterview Loop: If you and your recruiter still feel you could be well-suited for this position following your call, we will arrange our interview loop with some team members. This will allow you to learn more about the team and the organization and for us to gain a deeper understanding of your skills and experience.\u00a0 For this particular role, the interview loop is as follows:\nTechnical Interview: This will be a 60-minute Google Meet with two of our Senior Software Engineers. We ask you to come ready to present code in one of the following areas where you have previous experience: Numerical Computing, Distributed & High-Performance Computing, Infrastructure/Cloud, Data Science/Machine Learning/AI, MLOps, and/or Data Engineering.\nAfter presenting your code for 20-30 minutes, our engineers will ask follow-up questions. Then you will have time to discuss the day-to-day of working at Quansight and ask any questions you may have for them. If you do not have public code, we will provide a technical assessment.\nCase Study Interview: This phase of the interview process will include one interview with up to four members of our leadership team. This interview will be 90 minutes long and held over Google Meet. You will be asked in advance to prepare a project proposal to present as the first part of the interview.\u00a0\nYou will have time for questions and answers as the second part of the interview. Our recruiter will share more details with you before the interview is scheduled.\nFinal Leadership Interview: This will be a 60-minute Google Meet with our CTO and CEO to review the alignment of your professional goals and goals of the organization.\n\nFollowing the completion of all interviews, our hiring team will make a final decision, and the recruiter will be in touch to share the outcome of your interviews. If the team wants to move forward, the recruiter will discuss this and the details of our proposed offer with you.\n", "date": "2024-05-17", "id": "2024-05-17_quansight"}, {"entity": "Unitary Fund", "title": "Community Manager", "url": "https://unitary.fund/jobs/community_manager/", "percentTime": 100, "percentOSS": 100, "deadline": "2023-10-31", "expires": "2023-11-30", "location": "Remote", "description": "This opening is a rare opportunity to help run a new type of research\nnon-profit and contribute to the developing quantum technology ecosystem.\n\nUnitary Fund is a 501(c)(3) non-profit research group helping build a quantum technology industry that benefits the most people. We support the community, e.g., running a grant program to support the development of new open source quantum software, and we do our own research on key projects for the ecosystem.\n\nThis is a unique opportunity to have a direct impact on the growth of the quantum open source ecosystem and the tools that Unitary Fund is developing. We\u2019re hiring a Community Manager to help oversee the organization and execution of a diverse array of community building activities, including the Mitiq open-source community activities, Quantum Wednesdays talk series, hackathons, workshops, as well as ongoing resources like the Unitary Fund Discord Server, community surveys, newsletters and blog.\n\nYou will build sustainable and community-oriented open source practices to ensure the tools of the quantum ecosystem reach more people, sooner. This includes development and execution of in-person and online events, as well as outreach strategies to build new audiences and ensure support for existing users. You will work with a diverse array of community members including Unitary Fund Donors, Ambassadors, Advisory Board Members, Grantees, users advocates and more. This position is full time with a competitive salary range, commensurate with experience. Unitary Fund is a fully remote team and this role is also remote.\n", "date": "2023-09-01", "id": "2023-09-01_unitaryfund"}, {"entity": "Astropy", "title": "Research Software Engineer", "url": "https://www.astropy.org/", "percentTime": "50-100", "percentOSS": 100, "deadline": "2023-08-15", "expires": "2023-08-31", "location": "Remote", "description": "## About Astropy:\nThe Astropy Project is a community effort to develop a common core package for Astronomy in Python and\nfoster an ecosystem of interoperable astronomy packages. It is a widely used package in professional\nastronomy supporting missions like NASA\u2019s James Webb Space Telescope and the Square Kilometer Array.\nOur community has over 1,200 members with nearly 200 active weekly. The majority of our members are based\nin the US but many are from around the world. For more about the project goals, please look at our\n[roadmap](https://github.com/astropy/astropy-project/blob/main/roadmap/roadmap.md).\n\n## About the Position:\nThe Astropy Project is seeking to hire a full-time or part-time research software engineer to develop\nand implement new features that the community has identified as high priorities in the\n[roadmap](https://github.com/astropy/astropy-project/blob/main/roadmap/roadmap.md)\nbut that the volunteer contributors do not have the capacity to deliver, as well as address smaller,\nbut long-standing bugs and issues in the astropy core package and the coordinated packages.\nThis is the first time that the Astropy Project itself does a dedicated hire for this purpose,\nand thus it offers many opportunities to help build the role itself. As such, a driven and\nresourceful individual with experience in integrating with a mostly volunteer-run project would be\nbest suited for the role. If you are passionate about writing good software in the context of\nopen source software communities read on and apply. Applications before August 15th 2023 will\nreceive full consideration, but applications are accepted until the position is filled.\n\n### Key responsibilities of the role:\n- Debug and fix exiting issues in the astropy core and other packages in the Astropy ecosystem.\n- Plan, propose, discuss, and implement features as listed in the project roadmap](https://github.com/astropy/astropy-project/blob/main/roadmap/roadmap.md).\n- Revise documentation for developers and suggest and implement improvements to the workflow and tools to reduce workload on existing maintainers and barriers to entry for new developers.\n- Study and improve one or more of the infrastructure packages through pull requests and support the existing infrastructure team in ongoing maintenance\n- Interact with other developers on github, slack, and in developer telecons\n- Constructively review code and pull requests\n\n### The ideal candidate would have these skills:\n- Collaborative attitude.\n- Experience with a consensus-driven, geographically-dispersed software development team.\n- Experience in the scientific Python stack (Python, numpy, matplotlib)\n- Experience with continuous integration, writing tests, debugging, and pytest\n- Experience with git and github\n- Knowledge of scientific community engagement techniques\n\n## Desired Qualifications, but not necessary experiences include:\n- Ability to motivate others to step up for long-term involvement in the project\n- Experience in Python documentation and sphinx\n- Experience in one or more languages used for compiled Python extensions (C, Cython, rust)\n- Simple html/css skills\n- Ability to positively interact with other contributors and offer advice, review, and mentorship to enhance the knowledge and ability of others\n- Experience working with a multicultural, international community\n- Experience in documenting workflows for new contributors and maintainers\n- Knowledge of astronomy\n\n## Compensation and Term:\nThe software developer is funded by the Astropy Project through NumFocus;\nyou will invoice NumFOCUS for hours worked as an independent contractor.\nThe total funding available is US$75,000 to be spent by March 31, 2024.\nDepending on experience, we envision an hourly rate in the range 60-120 $/h,\nwhich is about 20-40 h/week for the period September 1st to March 31st.\nA lower level of effort or slightly longer time range can be accommodated,\nif explicitly requested in the application. There is a possibility of further\nextension: We would like this role to continue in the future, but this depends on\nfunding and feedback from the community. This work is fully remote and can be done anywhere on Earth.\n\n## How to Apply:\nIn order to apply, please send a cover letter, one page resume (ideally with links to previous pull requests or other contributions to open source projects), and a list of 2-3 names and contact information for people who have agreed to serve as a reference to Moritz G\u00fcnther at moritz.guenther@gmx.de with the subject line \u201cResearch Software engineer\u201d and your last name.\nFor questions about this position, please contact Moritz G\u00fcnther at moritz.guenther@gmx.de.\n", "date": "2023-07-31", "id": "2023-07-31_astropy"}, {"entity": "Aperio Software", "title": "Research Software Engineer", "url": "https://aperiosoftware.com", "percentTime": 100, "percentOSS": 80, "deadline": "2023-07-03", "expires": "2023-08-03", "location": "Remote", "description": "Aperio Software is a company that specialises in developing software and providing software training in scientific domains for a variety of organisations and open source projects.\nPast and current projects include the development of user tools and calibration software for the Daniel K. Inouye Solar Telescope (DKIST), work on the AAS WorldWide Telescope project, contributions to the Astropy and SunPy projects, and development of the glue visualisation tool.\n\nAperio is seeking a research software engineer and experienced open-source contributor to work with us on our growing number of projects in solar physics and astronomy, primarily in established open-source projects.\nThis will include working on the development, testing, documentation and maintenance of several different software packages and related infrastructure.\nYou will be required to liaise with clients, other staff, and the open-source community. \nFor more details see the full job description at [aperiosoftware.com/jobs](https://aperiosoftware.com/jobs).\n", "date": "2023-06-09", "id": "2023-06-09_aperio"}, {"entity": "Astropy Project", "title": "Community Manager", "url": "https://www.astropy.org/", "percentTime": 50, "percentOSS": 100, "deadline": "2023-05-31", "expires": "2023-07-01", "location": "Remote", "description": "## About Astropy:\nThe Astropy Project is a community effort to develop a common core package for Astronomy in Python and\nfoster an ecosystem of interoperable astronomy packages. It is a widely used package in professional\nastronomy supporting missions like NASA\u2019s James Webb Space Telescope and the Square Kilometer Array.\nOur community has over 1,200 members with nearly 200 active weekly. The majority of our members are based\nin the US but many are from around the world. For more about the project goals, please look at our\n[roadmap](https://github.com/astropy/astropy-project/blob/main/roadmap/roadmap.md).\n\n## About the Position:\nThe Astropy Project is seeking to hire a part-time community manager to help grow and foster our diverse\ncommunity of users, contributors, and developers. This is a new role in the organization offering many\nopportunities to help build our community and the role itself. As such, a driven and resourceful individual\nwould be best suited for the role. If you are passionate about engagement and equitable open source software\ncommunities read on and apply by May 31, 2023.\n\n### Key responsibilities of the role:\n- Manage and support the existing Astropy Project Community, in consultation with that community.\n- Work to grow the Astropy Project community, and identify opportunities for collaboration by establishing relationships with users,\nespecially from underrepresented communities and institutions.\n- Track and report engagement activities of the Astropy Project.\n- Run surveys to better understand the needs of our community and how to improve participation in our\ncommunities for different stakeholders.\n- Coordinate review and updates to the Astropy Code of Conduct, in consultation with the Astropy Coordination Committee.\n\n\n### The ideal candidate would have these skills:\n- Collaborative attitude.\n- Experience with a consensus-driven, geographically-dispersed software development team.\n- Ability to develop communication strategies to meet specific goals and reaching different audiences.\n- Knowledge of communities who use Github as their primary communication mechanism.\n- Demonstrable skills in group facilitation and stakeholder relationships management.\n- Knowledge of scientific community engagement techniques.\n- Experience and enthusiasm for Diversity, Equity, and Inclusion.\n- Strong interpersonal skills.\n- Excellent judgment and diplomacy.\n- Excellent written and oral communication skills.\n\n### Desired Qualifications, but not necessary experiences include:\n- Managing forums like Slack and email lists\n- Managing social media accounts.\n- Experience working with open source projects.\n- Organizing in-person meetings of ~20 people.\n- Using websites to communicate.\n- Experience with building consensus around a code-of-conduct.\n- Using surveys to shape strategy.\n- Developing strategies to build a user-maintainer-contributor pipeline.\n- Organizing in-person software workshops.\n- Experience of working with a multicultural, international community.\n- Experience moderating online discussion forums, blogging platforms, or similar.\n- Event and project management experience.\n- Awareness of current trends in academic culture and informal communication channels.\n- Experience using Github to communicate, edit documents, or develop software.\n\n## Compensation and Term:\nThe Community Manager is funded by Astropy Project which is managed by NumFocus.\nThe position is for a minimum duration of one year at US$48,000 with a possibility of further extension,\ndepending on the available funding.\nWe envision this role to be ongoing and future contracts to be for longer terms.\n\n## How to Apply:\nIn order to apply, please send a cover letter, one page resume, and a list of 2-3 names and contact information for people\nwho have agreed to serve as a reference to Ana Gabela at angabela@gmail.com with the subject line Community Manager and your last name.\n\nFor questions about this position, please contact Ana Gabela at angabela@gmail.com.\n", "date": "2023-04-07", "id": "2023-04-07_astropy"}, {"entity": "MDAnalysis", "title": "Project, community and outreach manager", "url": "https://contact.url", "percentTime": 100, "percentOSS": 100, "deadline": "2022-12-15", "expires": "2023-01-15", "location": "Remote", "description": "MDAnalysis is looking to hire a project, community and outreach manager. This is a remote full-time role on a 2-year contract, with possibility to renew subject to funding availability. The position will ideally start early in 2023.\nThe responsibilities of this role are listed below and cover a range of activities to better serve our community and enhance the range and scope of our outreach and dissemination activities.\n\n\n### Your profile\n\nYou are a proactive individual with a positive mindset and a can-do attitude. You have excellent communication skills and organisational/management experience. You are passionate about open source software in STEM and committed to advancing equity, diversity and inclusion in these communities.\n\n\n### Role responsibilities\n\n- **Expanding outreach and mentoring participation.** MDAnalysis has an established track record of participating in various mentoring schemes, including Google Summer of Code, Google Season of Docs, and Outreachy. You will undertake key administrative tasks for these schemes, including; applying on MDAnalysis\u2019s behalf, coordinating the project participation, contributing to the participant selection and supervising mentoring efforts. In addition, you will review and propose additional mentoring schemes MDAnalysis could participate in.\n\n- **Organising online workshops.** Together with the developer team, you will organise and help deliver a series of online workshops aimed at introducing new researchers in academia and industry to MDAnalysis, and showcasing how it can be integrated in their analysis workflows.\n\n- **Organising user group meetings (UGMs).** You will be tasked with organising one in-person user group meeting per year. UGMs are aimed at bringing together users and developers of the MDAnalysis package from different communities.\n\n- **ommunity bridge building.** You will build, maintain, and develop relationships between different open source projects in the computational chemistry/biophysics space to establish collaborations and coordinate development in a synergistic direction. This includes attending and representing MDAnalysis at community forums for other open source projects.\n\n- **Industrial partnerships.** A key goal of the project is to open up additional funding streams to sustain the project over the long term. To this end, you will build relationships with industrial partners, surveying their interest in adopting MDAnalysis and collecting information on desired features.\n\n- **Communicating with users and developers.** You will be responsible for updating the website, publishing blog posts, managing social media accounts and the Discord server, monitoring and communicating with users and developers on the mailing lists.\n\n- **Project management tasks.** You will manage and coordinate developer meetings, and provide administrative support to the project. This may include coordinating grant submission, liaising with funders and/or NumFOCUS. You will gather relevant metrics related to the project, which may include surveying users/developers.\n\n\n### Salary\n\n$75,000 + benefits (up to $96,000 gross). Exact take-home pay will depend on the tax laws of the country of residence.\nPlease note we are unable to arrange visas or work permits.\n\n### Location\n\nThis is a fully remote opportunity, with core team members in North America, Australasia and Europe and a global community of contributors. Due to the global span of the MDAnalysis community, this work will require working outside of the conventional 9-to-5 work day, for instance when attending team meetings.\n\n### Required criteria\n\n- Excellent communication and interpersonal skills\n\n- Familiarity with open source software related to any scientific discipline\n\n- Awareness and understanding of Diversity, Equity and Inclusion (DEI) initiatives and\nthemes\n\n- Organisational experience e.g., workshop/seminar/conferences, grant management,\ncross-institutional collaborations\n\n### Desired criteria\n\n- Familiarity with computational chemistry software\n\n- Familiarity with Python (or similar) and Jupyter notebooks\n\n- Familiarity with version control software and GitHub\n\n- Software development experience\n\n- Experience with coding documentation frameworks, (e.g. sphinx, jekyll, Markdown)\n\n- Experience with grant submission and management\n\n- Experience in leading or supporting initiatives or events focused on DEI\n\n### How to apply\n\nSend your CV (including 2 references) and a cover letter illustrating how your experience and qualifications match the job description to jobs@mdanalysis.org using the subject line: \u201cCZI project manager\u201d. \nWe will start reviewing applications on an ongoing basis from December 15th.\n", "date": "2022-11-11", "id": "2022-11-11_mdanalysis"}, {"entity": "OpenDP", "title": "Open-Source Community Manager", "url": "https://sjobs.brassring.com/TGnewUI/Search/Home/Home?partnerid=25240&siteid=5341#jobDetails=1950880_5341", "percentTime": 100, "percentOSS": 100, "deadline": "2022-12-31", "expires": "2022-12-31", "location": "On-Site and Remote Hybrid", "description": "OpenDP is an open-source initiative for building libraries, tools, and systems to share and analyze sensitive data with the strong mathematical protections of differential privacy.  It has a rapidly growing community that spans academia, industry, and government. The OpenDP community manager will assist the faculty directors, the executive committee, and the software development team in community outreach, engagement, governance, diversity, and inclusion.\n\nThis is a one year term appointment with the possibility of renewal.\n\n1. Plan and execute strategies to grow a diverse, inclusive open-source community, measuring, improving and reporting on metrics about diversity, equity and inclusion in the OpenDP community.\n2. Engage with stakeholders within and outside the OpenDP community; manage communication and outreach on a regular basis over a variety of channels, such as website, email, mailing list, Github repos, GitHub Discussions, Slack, Twitter, etc.\n3. Implement and manage the onboarding process for new community members and contributors to the OpenDP project.\n4. Assist with developing and communicating the processes for contribution and vetting.\n5. Organize and manage OpenDP GitHub repos and issues; review and update existing GitHub issues for clarity and contributor-friendliness.\n6. Review, prioritize, and track user requests and feedback via a variety of channels as mentioned above; assist with outreach to potential new users of the OpenDP software.\n7. Organize and manage community working groups addressing specific goals and tasks\n8. Organize virtual and in-person events for the community, such as community meetings, workshops and webinars.\n9. Assist with roadmap planning by advocating for community needs.\n10. Guide OpenDP developers, stakeholders, and community members in documenting user stories, technical specifications, and contributor and user guides.\n11. Curate resources for community learning and training, in both differential privacy and the specifics of OpenDP.\n\n### Basic Qualifications\n\n* Minimum of two years' post-secondary education or relevant work experience\n\n### Additional Qualifications and Skills\n\n* Exposure to and interest in learning more about differential privacy.\n* Experience and interest in working with open-source projects and communities.\n* Excellent written and verbal communication skills.\n* Excellent interpersonal skills and empathy, valuing diversity and working with communities. \n\n### Additional Information\n\nSEAS is not able to provide visa sponsorship for this position.\n\nThis is a one year term appointment, with the possibility of renewal.\n\n* The University requires all Harvard community members to be fully vaccinated against COVID-19 and remain up to date with COVID-19 vaccine boosters, as detailed in Harvard's Vaccine & Booster Requirements. Individuals may claim exemption from the vaccine requirement for medical or religious reasons. More information regarding the University's COVID vaccination requirement, exemptions, and verification of vaccination status may be found at the University's \"COVID-19 Vaccine Information\" webpage: http://www.harvard.edu/coronavirus/covid-19-vaccine-information/. \n* This is an in-person position based on our campus in Allston, MA.  Due to the COVID-19 pandemic, it is possible that the work may begin with a hybrid model.  All remote work must be performed in a state in which Harvard is registered to do business (CA, CT, MA, MD, ME, NH, NY, RI, and VT). The remote nature of this job is only temporary.  In-person on-campus work will be required as a condition of continued employment once the University and your supervisor determine that it is appropriate. Individual flexible and remote work options for this role can be discussed during the interview process. \n\n\n### About SEAS\nThe Harvard John A. Paulson School of Engineering and Applied Sciences serves as the connector and integrator of Harvard's teaching and research efforts in engineering, applied sciences, and technology. Our core tenets are educating broad-minded students; interdisciplinary research; integration across disciplines; and balancing theory, experimentation and practice to create an unmatched environment for learning and exploration. Through collaboration with researchers from all parts of Harvard, other universities, and industry, we bring discovery and innovation directly to bear on improving human life and society. For more information about us, please visit www.seas.harvard.edu.\n\n### Commitment to Equity, Diversity, Inclusion, and Belonging\nHarvard University views equity, diversity, inclusion, and belonging as the pathway to achieving inclusive excellence and fostering a campus culture where everyone can thrive. We strive to create a community that draws upon the widest possible pool of talent to unify excellence and diversity while fully embracing individuals from varied backgrounds, cultures, races, identities, life experiences, perspectives, beliefs, and values.\n\n### EEO Statement\n\nWe are an equal opportunity employer and all qualified applicants will receive consideration for employment without regard to race, color, religion, sex, national origin, disability status, protected veteran status, gender identity, sexual orientation, pregnancy and pregnancy-related conditions, or any other characteristic protected by law.\n", "date": "2022-09-02", "id": "2022-09-02_opendp"}, {"entity": "Unitary Fund", "title": "Chief of Staff", "url": "https://unitary.fund/jobs/chief_of_staff.html", "percentTime": 100, "percentOSS": 80, "deadline": "2022-07-26", "expires": "2022-09-26", "location": "Remote", "description": "This opening is a rare opportunity to help run a new type of research\nnon-profit and contribute to the developing quantum technology ecosystem.\n\nUnitary Fund is a 501(c)(3) non-profit working to create a quantum\ntechnology ecosystem that benefits the most people.\nWe run a grant program to support new open source quantum tech,\nand we fund our own research on key projects for the ecosystem.\n\n\nWe\u2019re looking for a proactive generalist who can contribute and learn\nacross the organization. They will work directly with the President and\nthe CTO to manage and scale processes and projects.\n\nExamples can include scaling our microgrant process, raising funds from\nnew supporters, managing our bounty programs and hackathons,\nbringing on new partner organizations and new geographies,\ncontracting a new website build, and other special projects.\n\nThis role encompasses work across multiple aspects of the organization\n(microgrants and Unitary Labs) and will require a strongly organized\nindividual to surface priorities, triage needs, communicate continuously,\nnd know when to ask for help. This position will work closely with\nUnitary Fund leadership and have opportunities to develop their own\nleadership as well. We are looking for someone excited to take ownership\nof the projects that they work on.\n", "date": "2022-07-26", "id": "2022-07-26_unitaryfund"}, {"entity": "Spyder", "title": "Spyder Junior Developer", "url": "https://tinyurl.com/spyder-grant-job", "percentTime": 100, "percentOSS": 100, "deadline": "2022-05-22", "expires": "2022-05-23", "location": "Remote", "description": "[Spyder](https://www.spyder-ide.org/) is a free and open source scientific \nenvironment written in Python, for Python, and designed by and for \nscientists, engineers and data analysts. It features a unique combination \nof the advanced editing, analysis, debugging, and profiling functionality \nof a comprehensive development tool with the data exploration, interactive \nexecution, deep inspection, and beautiful visualization capabilities of a \nscientific package.\n\nWe are looking for someone to help us improve the installation experience \nfor the Spyder IDE Windows users, by enabling the current standalone \ninstallers available to be auto-updatable and providing a way to install \ncustom packages that are not bundled with the installers in a simpler way.\n\nThe full job/project description and application process is available on \n[this document](https://docs.google.com/document/d/e/2PACX-1vTUQDK4zrmwJ0E_KQehqmAjWWBFM8aJSlxCOY4m8k64pYnhYn81OMg6VaCPKXdx3DHjbSbxWjOlUMEs/pub)\n", "date": "2022-04-29", "id": "2022-04-29_spyder"}, {"entity": "ArviZ", "title": "Dataviz External Consultant", "url": "https://github.com/arviz-devs/GSoD/blob/main/2021/viz_evaluation.md", "percentTime": "NA", "percentOSS": 100, "deadline": "2022-07-31", "expires": "2022-07-31", "location": "Remote", "description": "Dataviz is a key part of any statistical or data science job, and Bayesian\nmodels are not an exception. ArviZ provides tools for Exploratory Analysis\nof Bayesian Models, from diagnostics and summary statistics to bespoke\nvisualizations.\n\nWe are searching for someone to analyze the current state of available\nplots to identify missing visualizations, defaults not following bad\npractices and missing references in the documentation and write a\nreport with the findings and recommendations. The report will be published\nin hopes it also helps other dataviz libraries.\n\nThe full job description and application process is available on\n[our GitHub](https://github.com/arviz-devs/GSoD/blob/main/2021/viz_evaluation.md)\n", "date": "2022-03-12", "id": "2022-03-12_arviz"}, {"entity": "NVIDIA", "title": "PyData Open Source Developer", "url": "https://tinyurl.com/nvidia-pydata-job", "percentTime": 100, "percentOSS": 80, "deadline": "2022-09-30", "expires": "2022-09-30", "location": "Remote", "description": "Hundreds of thousands of AI engineers, data scientists, and data engineers rely \non both NVIDIA technologies and related PyData open source projects to do their \nlife's work. We are continuing to expand our support of the PyData ecosystem and \nlooking to hire strong engineers who are or can become contributors to libraries \nlike Pandas, Scikit-learn, NumPy, and more! This is an excellent opportunity for \nyou to balance between contributing to and helping users of these \nopen source projects.\n\nA full job description and application form can be found on the \n[NVIDIA career site job posting](https://nvidia.wd5.myworkdayjobs.com/NVIDIAExternalCareerSite/job/US-CA-Remote/Senior-Software-Engineer--PyData-Open-Source-Developer---RAPIDS_JR1954810).", "date": "2022-03-01", "id": "2022-03-01_nvidia"}, {"entity": "TU Delft & SymPy", "title": "Postdoctoral Researcher Opening: Advancing Biomechanical Modeling By Improving SymPy Code Generation (x/f/m)", "url": "https://tinyurl.com/sympy-job-2022", "percentTime": 100, "percentOSS": 100, "deadline": "2022-02-28", "expires": "2022-03-01", "location": "Delft, Netherlands with possibility of remote", "description": "The TU Delft Bicycle Lab is seeking a postdoctoral researcher for a 1\nyear equivalent contract (full or part time). You will work on a\nsub-project of [SymPy\\'s \\\"Improving Foundational Open Source Symbolic\nMathematics for Science\\\"\ngrant](https://chanzuckerberg.com/eoss/proposals/sympy-improving-foundational-open-source-symbolic-mathematics-for-science/)\nfunded by the [Chan-Zuckerberg Foundation's \"Essential Open Source\nSoftware for Science\\\"\nprogram](https://chanzuckerberg.com/newsroom/czi-awards-16-million-for-foundational-open-source-software-tools-essential-to-biomedicine/).\nThe purpose of the overall grant is to improve the SymPy Python symbolic\nmathematics library in the key areas of performance, code generation,\nand documentation. The [full grant\nproposal](https://doi.org/10.6084/m9.figshare.16590053.v1) is available\npublicly on Figshare.\n\n**Applications Due February 28, 2022**\n\nMore information about the job can be found at\nhttps://mechmotum.github.io/blog/sympy-czi-postdoc.html\n", "date": "2022-02-04", "id": "2022-02-04_sympy"}, {"entity": "rOpenSci", "title": "Community Manager", "url": "https://ropensci.org/careers/community-manager-2022", "percentTime": 100, "percentOSS": 100, "deadline": "2022-03-01", "expires": "2022-03-02", "location": "Remote", "description": "rOpenSci seeks a _Community Manager_ to support and build our vibrant and inclusive community\nof researchers and software engineers and help expand the scope and impact of our work in\nopen science.\n\nrOpenSci is a not-for-profit organization with the mission to expand access to scientific data\nand promote a culture of reproducible research and sustainable research software.\nrOpenSci staff and community are fully remote, with core team members in North America and\nEurope and a global community of contributors.  We're looking for a new community manager\nto help us to consolidate our existing efforts and ensure that we're supporting an increasingly\ndiverse membership.\n\nThis is a long-term, full-time contract position with a salary range of $70,000-$85,000,\ncommensurate with experience, skills, and local benefits requirements.  The position is 100%\nremote. Costs related to a home office or co-working space will be supported,\nas well as professional development opportunities,\n\nA full job description and application form can be found at <https://ropensci.org/careers/community-manager-2022/>.", "date": "2022-02-03", "id": "2022-02-03_ropensci"}, {"entity": "Unitary Fund", "title": "Member of the Technical Staff (part-time)", "url": "https://unitary.fund/jobs/mts.html", "percentTime": 40, "percentOSS": 100, "deadline": "2022-02-13", "expires": "2022-02-13", "location": "Remote", "description": "We\u2019re hiring Members of Technical Staff with a focus on Mitiq.\nYou'll be part of the core development team of [Mitiq](https://github.com/unitaryfund/mitiq), the open-source quantum\nerror mitigation toolkit, in collaboration with key players from the industry and research sector.\n\nUnitary Fund is a 501(c)(3) non-profit working to create a quantum technology ecosystem that benefits the most people.\nWe run a grant program to support new open source quantum tech, and we fund our own research on key projects for the ecosystem.\nWe\u2019re hiring for a part-time marketing and communications role on our team.\nThis opening is a rare opportunity to contribute to the developing quantum technology ecosystem.\nThe full job description can be found [here](https://unitary.fund/jobs/mts.html)", "date": "2022-01-13", "id": "2022-01-13_unitaryfund"}, {"entity": "GNU Radio", "title": "Technical writer", "url": "https://www.gnuradio.org/news/2021-11-30-numfocus-jobs/", "percentOSS": 100, "deadline": "2022-04-03", "expires": "2022-04-03", "location": "Remote", "description": "GNU Radio has recently won a small grant from NumFOCUS to help improve GNU Radio\u2019s tutorials, as they play a major role in helping new users get through the steep learning curve. We are looking to hire a part-time contractor to perform the work of one GNU Radio expert. It is anticipated that this work will be performed during Q1 of 2022.\n\n### Rate\n\n$800 total, $40 per hour, 20 hours total\n\n### Task\n\n* Starting at the top of the tutorial list and working your way down (or using an order of your choosing), go through as many tutorials as the funding/time allows, and do a single editing pass through each one, to improve clarity and writing quality. Readability is more important than using \u201cproper technical style\u201d; many readers aren\u2019t going to have English as their first language. Any improvements to visual presentation, such as wiki formatting to make the content look better (e.g., shrink an image that is clearly way too large compared to the surrounding content), is also desired.\n\nEmail jobs@gnuradio.org if you are interested in either of these positions. Thanks!\n", "date": "2021-12-03", "id": "2021-12-03_gnuradio"}, {"entity": "GNU Radio", "title": "GNU Radio Expert", "url": "https://www.gnuradio.org/news/2021-11-30-numfocus-jobs/", "percentOSS": 100, "deadline": "2022-04-01", "expires": "2022-04-01", "location": "Remote", "description": "GNU Radio has recently won a small grant from NumFOCUS to help improve GNU Radio\u2019s tutorials, as they play a major role in helping new users get through the steep learning curve. We are looking to hire a part-time contractor to perform the work of one GNU Radio expert. It is anticipated that this work will be performed during Q1 of 2022.\n\n### Rate\n\n$3600 total, $60 per hour, 60 hours total spread across at least 1 month (max 3 months)\n\n### Tasks (tackle one or more of the following tasks)\n\n* Complete overhaul of the QPSK and BPSK tutorials reflecting the change from \u2018Polyphase Clock Sync\u2019 to \u2018Symbol Sync\u2019, and from \u2018CMA Equalizer\u2019 to \u2018Linear Equalizer\u2019 and \u2018Adaptive Algorithm\u2019.\n* The Embedded Python Block tutorial should be the first tutorial that dives into how to write a block (in Python) but it\u2019s currently performing double duty as the Embedded Python Block \u201cblock docs\u201d page, so the task is to separate them into two pages and improve the tutorial itself.\n* Pick one or more of our existing tutorials and propose what you will do to improve it, then get approval from the GNU Radio leadership, and then execute on the proposed improvements. The step prior to approval cannot involve billing more than 4 hours.\n* If the contractor has any specific expertise, e.g. in an application of GNU Radio we don\u2019t currently have a tutorial about, the contractor can create a new tutorial after getting GNU Radio leadership approval.\n\nEffort should be spread relatively evenly across at least 1 month (with a max of 3 months), so that the GNU Radio Expert has time to share midterm progress with the GNU Radio leadership and obtain feedback. To enforce this, we will limit the amount billed each week to no more than 16 hours. The effort must be completed within 3 months after the start of work.\n\nEmail jobs@gnuradio.org if you are interested in either of these positions. Thanks!\n", "date": "2021-11-30", "id": "2021-11-30_gnuradio"}, {"entity": "Zarr", "title": "Community Manager", "percentTime": 100, "percentOSS": 100, "deadline": "2021-12-15", "expires": "2021-12-21", "location": "Remote", "description": "Zarr is a format for the storage of chunked, compressed, N-dimensional arrays.\nBuilt originally in Python for working with NumPy arrays, Zarr is now supported\nin more than half a dozen languages. With funding from the Chan Zuckerberg\nInitiative, we are looking to hire a full-time, open-source enthusiast for two\nyears to work as our community manager.\n\nAbout the Role:\n\nAs the first full-time position dedicated to Zarr, you will have the\nopportunity to shape the role to meet the needs of the community. You will be\nan advocate for our strong, inclusive community, as well as define ways to\nencourage new contributions while balancing the needs of a maturing data\nformat.\n\nWe are looking for someone who can:\n\n* Foster and build relationships online\n* Communicate clearly and technically with open-source developers\n* Be a conduit to the research user communities\n* Structure the project for growth and resiliency\n\nThis is a fully remote position. Responsibilities:\n\n* Work with open source contributors to review, refine, and merge contributions\n* Identify and mentor potential new core-developers\n* Design a specification process that fosters quick decisions on emerging user cases\n* Report developments via social media, the Zarr website and scientific publications\n* Plan, organize, and run regular community calls\n* Attend online and in-person events to speak about the project\n* Help to build a welcoming, inclusive, and supportive community environment!\n\n## Experience:\n\nCandidates should have experience in one or more programming languages with a\nstrong tendency towards Python and be comfortable working with Git and GitHub.\n\n## Compensation:\n\nThis role can be either a contract position or an employed position with fringe\nbenefits. $60,000 \u2013 $80,000 per year dependent on position type and experience.\nIf you are looking for a flexible, open-source position that can make a world\nof difference in bioimaging, climate science, or more. Please get in touch via\nhiring@numfocus.org.\n", "date": "2021-11-18", "id": "2021-11-18_zarr"}, {"entity": "Quansight", "title": "Sr. Frontend Engineer", "url": "https://quansight.breezy.hr/p/47a9c3e35069-senior-frontend-software-engineer", "percentTime": 100, "percentOSS": 60, "badges": ["Sponsor"], "deadline": "2021-10-13", "expires": "2021-10-13", "location": "Remote", "description": "We are looking for a Senior Frontend Engineer to join Quansight Labs and our \nmission of supporting the PyData open-source ecosystem. \nAs a Senior Frontend Engineer, you will lead our internal web-related projects \nand contribute to the community-driven open-source projects supported by \nQuansight Labs. We are seeking great engineers who are passionate about web \ndevelopment, open source, accessibility and who possess a collaborative and \ncommunity-first mindset.\n", "date": "2021-10-13", "id": "2021-10-13_quansight"}, {"entity": "Unitary Fund", "title": "Communications and Marketing (part-time)", "url": "https://unitary.fund/jobs/marketing_comm.html", "percentTime": 25, "percentOSS": 100, "deadline": "2021-11-19", "expires": "2021-11-19", "location": "Remote", "description": "Unitary Fund is a 501(c)(3) non-profit working to create a quantum technology ecosystem that benefits the most people. \nWe run a grant program to support new open source quantum tech, and we fund our own research on key projects for the ecosystem. \nWe\u2019re hiring for a part-time marketing and communications role on our team. \nThis opening is a rare opportunity to contribute to the developing quantum technology ecosystem.\nThe full job description can be found [here](https://unitary.fund/jobs/marketing_comm.html)\n", "date": "2021-10-12", "id": "2021-10-12_unitaryfund"}, {"entity": "Quansight", "title": "Contributor Experience Lead", "url": "https://quansight.breezy.hr/p/361a2e7da19b-contributor-experience-lead", "percentTime": 100, "percentOSS": 100, "badges": ["Sponsor"], "deadline": "2021-10-31", "expires": "2021-10-31", "location": "Remote", "description": "We are looking for a Contributor Experience Lead, who will work across\nprojects to identify, document, and implement practices to foster inclusive\nopen-source communities for NumPy, SciPy, Matplotlib, and Pandas.\n", "date": "2021-09-09", "id": "2021-09-09_quansight"}, {"entity": "Aperio Software", "title": "Software Developer", "url": "https://aperiosoftware.com", "percentTime": 100, "percentOSS": 80, "deadline": "2021-05-14", "expires": "2021-05-30", "location": "Remote", "description": "Aperio Software is a company that specialises in developing software and providing software training in scientific domains for a variety of organisations and open source projects.\nPast and current projects include the development of user tools and calibration software for the Daniel K. Inouye Solar Telescope (DKIST), work on the AAS WorldWide Telescope project, contributions to the Astropy and SunPy projects, and development of the glue visualisation tool.\n\nAperio is seeking a software developer and experienced open-source contributor to work with us on our growing number of projects in solar physics and astronomy, primarily in established open-source projects.\nThis will include working on the development, testing, documentation and maintenance of several different software packages.\nYou will be required to liaise with clients, other staff and the open-source community. \nIn addition, there will be the opportunity to get involved in designing and delivering programming workshops.\nFor more details see the full job description at [aperiosoftware.com/jobs](https://aperiosoftware.com/jobs).\n", "date": "2021-04-30", "id": "2021-04-30_aperio"}, {"entity": "Kitware", "title": "Medical Image Analysis Researcher", "url": "https://www.kitware.com/careers", "percentTime": 100, "percentOSS": 75, "deadline": "2021-10-30", "expires": "2021-10-30", "location": "Carrboro, NC", "description": "Team Description: \nKitware's Medical Computing team solves complex healthcare challenges through customized software solutions. The team provides research, development, and technology integration services to research centers, universities and companies in the medical and biomedical sectors. By joining our team you will participate in a dynamic multidisciplinary work environment with exceptionally talented computer scientists, software engineers, mechanical engineers and biomedical engineers.| \n\nAbout the projects: \nWe build powerful software solutions using our well-established open source platforms including VTK, ITK, 3D Slicer, Pulse, iMSTK, CMake and ParaView. These platforms are mature, supported, documented, and thoroughly tested software systems that are in use by tens of thousands of developers and end-users worldwide. Our commercial projects involve generating solutions for medical device companies from ultrasound and preclinical imaging companies, to surgery device companies. Our research projects involve collaborations with top research institutions, including Harvard Medical School, MIT, Cleveland Clinic, Duke University, and others and build on our open source platforms.\n", "date": "2021-04-30", "id": "2021-04-30_kitware"}, {"entity": "The Julia Language", "title": "Technical Writer", "url": "https://discourse.julialang.org/t/the-julia-language-is-hiring-a-technical-writer-as-part-of-google-season-of-docs-2021/59806", "percentTime": 25, "percentOSS": 100, "deadline": "2021-05-10", "expires": "2021-05-10", "location": "Remote", "description": "The Julia Language is excited to share that we will be hiring a technical writer as part of Google Season of Docs 2021. For more details, check out the [full job post](https://discourse.julialang.org/t/the-julia-language-is-hiring-a-technical-writer-as-part-of-google-season-of-docs-2021/59806).\n", "date": "2021-04-22", "id": "2021-04-22_julia"}, {"entity": "Coiled", "title": "OSS Engineer", "url": "https://coiled.io/careers/", "percentTime": 100, "percentOSS": 80, "badges": ["Sponsor"], "deadline": "2021-10-14", "expires": "2021-10-14", "location": "Remote", "description": "This role is for a software engineer to work on the open source Dask project.\nYou will be able to build and maintain Dask and its connections with surrounding\nprojects.\n\nThis role will also ensure that Coiled customers are effective in their use of\nDask to scale Python. You will interact with users (both community and clients),\nengage in team prioritization, and work to implement fixes and features to improve\nthe experience and capabilities of the Python ecosystem, verifying that those fixes\nresolve user needs.\n", "date": "2021-04-14", "id": "2021-04-14_coiled"}, {"entity": "Quansight", "title": "PyData maintainer", "url": "https://www.quansight.com/careers", "percentTime": 100, "percentOSS": 60, "badges": ["Sponsor"], "deadline": "2021-10-13", "expires": "2021-10-13", "location": "Remote", "description": "This is an open hiring call for candidates who are already heavily invested\nin contributing to the PyData open source ecosystem and are looking for a\ncompany that can support their passion. The specifics of your role will be\ndetermined during the interview process.\n", "date": "2021-04-13", "id": "2021-04-13_quansight"}]
export default jobs;
